.banner {
  color: white;
  object-fit: contain;
  height: 448px;
}

.banner__contents {
  margin-left: 30px;
  padding-top: 140px;
  height: 190px;
}

.banner__title {
  font-size: 3rem;
  font-weight: 800;
  padding-bottom: 0.3rem;
}

.banner__description {
  width: 408rem;
  font-size: 0.8rem;
  line-height: 1.3rem;
  padding-top: 1rem;
  max-width: 360px;
  height: 80px;
}

.banner__button {
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 500;
  border-radius: 8px;
  padding-left: 2rem;
  padding-right: 2rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  margin-right: 1rem;
  font-size: 1rem;
}

.banner__button:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}

.banner__fadeBottom {
  height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgba(37, 37, 37, 0.61),
    #111
  );
}

.balButton {
  width: 380px;
  position: absolute;
  right: 260px;
  object-fit: contain;
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 500;
  border-radius: 8px;
  padding-left: 2rem;
  padding-right: 2rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  margin-right: 1rem;
  font-size: 1rem;
}

.balButton__text {
  font-size: 0.8rem;
  font-weight: 400;
  font-style:oblique;
}

.balButton:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}

.nav__avtar {
  width: 225px;
  position:absolute;
  right: 10px;
  object-fit: contain;
  cursor: pointer;
  color: #fff;
  outline: none;
  border: none;
  font-weight: 500;
  border-radius: 8px;
  padding-left: 2rem;
  padding-right: 2rem;
  background-color: rgba(51, 51, 51, 0.5);
  padding-bottom: 0.5rem;
  padding-top: 0.5rem;
  margin-right: 1rem;
  font-size: 1rem;
}

.nav__avtar:hover {
  color: #000;
  background-color: #e6e6e6;
  transition: all 0.2s;
}